
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import { mapGetters, mapMutations, mapState,mapActions } from "vuex";
import { getAjaxData, postAjaxData } from "api/api";
import UserSelect from "../../components/User/template";
import Dict from "@/components/dict";

export default {
components: {
XHeader,
Group,
Cell,
XInput,
Datetime,
dateFormat,
XTextarea,
PopupRadio,
PopupPicker,
XButton,
UserSelect,
Dict
},
created(){
this.formData.GetDate=dateFormat(new Date(), "YYYY-MM-DD")
this.initPage()
},
beforeRouteLeave (to, fromb, next) {
if(!this.backConfirm){
this.$vux.confirm.show({
title:"继续退出？",
content:"您已编辑过，退出将丢失这些内容！",
onCancel () {
next(false)
},
onConfirm () {
next()
}
});
}else{
next()
}
},
mounted() {

this.$bus.on("selectAddCustomerAddress", val => {
if (val) {
if(val.province && val.city){
this.setCusmoterArea({
province: val.province,
city: val.city,
area: val.district
});
}

this.formData.Address = val.address + val.title;
this.formData.MapLat = val.point.lat;
this.formData.MapLng = val.point.lng;
this.customerAddress = val.address + val.title;
}
});
},
data() {
return {
backConfirm: false,        //点击后退时是否弹出确定框
buttonAble:false,
userBool:false,
sexList: [
{
key: 1,
value: "男"
},
{ key: 2,
value: "女"
}
],
fromList: [
{ key: 0, value: "自建" },
{ key: 1, value: "迈迪网注册企业" }
],
sourceList:[
{
key: 1,
value: '转介绍'
},
{
key: 2,
value: '线上注册'
},
{
key: 3,
value: '线上询价'
},
{
key: 4,
value: '预约上门'
},
{
key: 5,
value: '陌拜'
},
{
key: 6,
value: '招商资源'
},
{
key: 7,
value: '公司资源'
},
{
key: 8,
value: '展会资源'
},
{
key: 9,
value: '个人资源'
},
{
key: 10,
value: '电话咨询'
},
{
key: 11,
value: '邮件咨询'
},
],

user: {
province: "",
city: "",
area: ""
},
isRegister:0,
isGetCompByMdt:false,
Mdt:'',
formData: {
ID: 0,
GetDate: "",
Remark: "",
Tel: "",
CustomerName: "",
SaleChief: "",
SaleChiefName: "",
Province: "",
City: "",
District: "",
Street: "",
Address: "",
CustState: "", //客户状态
CustStateName: "", //客户状态
CustType: "", //客户类型
CustTypeName: "", //客户类型
CustLevel: "", //客户等级
CustLevelName: "", //客户等级
CustFrom: 1, //客户来源
CustFromName: "", //客户来源
MaturityType: "", //客户成熟度
MaturityTypeName: "", //客户成熟度
LinkMans: [],
CustCompID:0,
MapLat: 0,
MapLng: 0
},
linkMan: {
ID: 0,
UserID: 0,
LinkName: "",
LinkSex: 1,
LinkPhone: "",
LinkEmail: "",
LinkPost: "",
LinkTel: "",
Birthday: "",
IsChief: 1,
IsDelete: 0
},
name: "",
pageView: "add", //客户添加修改判断项
addCustomer: false, //是否添加联系人
showMore: true 
};
},
computed: {

...mapGetters(["getCustomerArea", "getCustomerAddress"]),
...mapState({
customerArea: "customerArea",
dict:"dict"
}),

displayArea: function() {
if (this.formData.Province) {
if (this.formData.City) {
return (
this.formData.Province +
"/" +
this.formData.City +
"/" +
this.formData.District
);
} else {
return this.formData.Province;
}
} else {
return "";
}
},

customerAddress: {
get() {
return this.$store.state.customerAddress;
},
set(value) {
this.$store.commit("SET_CUSTOMERADDRESS", value);
}
},

customerGeoAddress: {
get() {
return this.$store.state.customerGeoAddress;
},
set(value) {
this.$store.commit("SET_CUSTOMERGEOADDRESS", value);
}
},
isLocation(){
return this.formData.MapLat&&this.formData.MapLng;
}
},

beforeDestroy() {

this.setCustomerAddress("");
this.setCusmoterArea({ province: "", city: "", area: "" });
this.$bus.off("selectAddCustomerAddress");
},
methods: {

...mapMutations({
setCustomerAddress: "SET_CUSTOMERADDRESS",
setCusmoterArea: "SET_CUSTOMERAREA",
setSelectCustomer:"setSelectCustomer"
}),
...mapActions(["initDictPromise"]),

onInputFocus(val, $event){
console.log($event)
},

goAddCustomerSonPage(path, name) {
this.$vux.toast.hide()
this.$router.push({
path,
query: {
name,
lat:this.formData.MapLat,
lng:this.formData.MapLng,
}
})
},
saveUser(user){
if(user && user.ID){
this.formData.SaleChief = user.ID
this.formData.SaleChiefName = user.OrgName
this.userBool=false
}
},
getRegistCompInfo(){
if(!this.Mdt){
this.$vux.toast.show({
text: "请输入迈迪通号",
position: "middle",
type: "warn"
});
return false;
}

let _url = `/Comp/GetCompInfoByMdt?mdt=${this.Mdt}`
this.Axios.get(_url).then(res=>{
let _data = res.data;
if(_data.State == 1){
this.isGetCompByMdt = true
this.formData.CustomerName = _data.Data.Name
this.formData.CustCompID = _data.Data.ID
this.formData.Province = _data.Data.Province
this.formData.City = _data.Data.City
this.formData.District = _data.Data.District
this.formData.MapLat =   _data.Data.MapLat
this.formData.MapLng =   _data.Data.MapLng
this.linkMan.LinkName =_data.Data.MainLinkName
this.linkMan.LinkPhone = _data.Data.MainLinkPhone
this.linkMan.LinkEmail = _data.Data.Email
this.setCusmoterArea({ province:  this.formData.Province,
city: this.formData.City,
area: this.formData.District });
}else{
this.isGetCompByMdt = false
this.$vux.toast.show({
text: "获取企业信息失败",
position: "middle",
type: "cancel"
});
}
})
},
async initPage(){
this.buttonAble = false;
if (this.$route.query.cId) {
this.pageView = "change";
this.getCustomerInfo(this.$route.query.cId);
}
},
initTypeDict(num){
return new Promise((resolve, reject)=>{
this.initDictPromise(num).then(()=>{
let list = []
this.dict[num].forEach(item =>{
list.push({
value: item.DictName,
key: `${item.ID}`
})
})
resolve(list)
})
})

},
gotoLocation(){
let _this = this;
this.$nextTick(function(){
setTimeout(function(){
if(_this.formData.ID == 0){
_this.goAddCustomerSonPage('/addClient/addressMap','定位')
}else{
_this.goAddCustomerSonPage('/client/clientDetails/addClient/addressMap','定位')
}
}, 500);

})

},
routerBack() {
this.$router.back();
},

getCustomerInfo(name) {
this.$vux.loading.show({
text: "请稍候..."
});
let url = "/Customer/GetCustomerInfoByID";
getAjaxData(url, { id: name }).then(res => {
this.$vux.loading.hide();
if (res.State === 1) {
Object.assign(this.formData, res.Data);
this.formData.GetDate = dateFormat(
this.formData.GetDate,
"YYYY-MM-DD"
);
this.setCusmoterArea({ province: this.formData.Province, city: this.formData.City, area: this.formData.District});
} else {
this.$vux.toast.show({
text: "网络出现问题",
position: "middle",
type: "cancel"
});
}
});
},

addCustomerSubmit() {
if(this.isRegister && !this.isGetCompByMdt){
this.$vux.toast.show({
text: "请输入正确的迈迪通号",
position: "middle",
type: "warn"
});
return;
}
if(this.formData.CustomerName.length > 50){
this.$vux.toast.show({
text: "客户名称字段过长",
position: "middle",
type: "warn"
});
return;
}
if(this.formData.ID==0){
if (this.isRegister==='') {
this.$vux.toast.show({
text: "请选择客户来源",
position: "middle",
type: "warn"
});
return;
}
}

if (this.isRegister==1 && !this.Mdt) {
this.$vux.toast.show({
text: "请输入迈迪通号",
position: "middle",
type: "warn"
});
return;
}
if(!this.isLocation){
this.$vux.toast.show({
text: "请定位选择企业地址",
position: "middle",
type: "warn"
});
return false
}
if (!this.formData.GetDate) {
this.$vux.toast.show({
text: "请选择获得客户的时间",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.CustomerName) {
this.$vux.toast.show({
text: "客户名称不能为空",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.CustState) {
this.$vux.toast.show({
text: "请选择客户状态",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.CustType) {
this.$vux.toast.show({
text: "请选择客户类别",
position: "middle",
type: "cancel"
});
return;
}
if (!this.formData.CustLevel) {
this.$vux.toast.show({
text: "请选择客户等级",
position: "middle",
type: "warn"
});
return;
}
if (!this.formData.MaturityType) {
this.$vux.toast.show({
text: "请选择客户成熟度",
position: "middle",
type: "warn"
});
return;
}

if (!this.formData.Remark) {
this.$vux.toast.show({
text: "请填写客户介绍",
position: "middle",
type: "warn"
});
return;
}
if(!this.displayArea){
this.$vux.toast.show({
text: "请选择客户所在城市",
position: "middle",
type: "warn"
});
return false;
}
if(!this.formData.Address){
this.$vux.toast.show({
text: "请填写公司地址",
position: "middle",
type: "warn"
});
return false;
}
if(this.formData.ID == 0){
if (!this.linkMan.LinkName) {
this.$vux.toast.show({
text: "请填写联系人姓名",
position: "middle",
type: "warn"
});
return;
}
let pattern1 = /^1[34578]\d{9}$/
if (
!pattern1.test(this.linkMan.LinkPhone) &&
this.linkMan.LinkPhone != ""
) {
this.$vux.toast.show({
text: "联系人手机号不正确",
position: "middle",
type: "warn"
});
return;
}
let pattern2 = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
if(this.linkMan.LinkEmail!="" && !pattern2.test(this.linkMan.LinkEmail)){
this.$vux.toast.show({
text: "联系人邮箱格式不正确",
position: "middle",
type: "warn"
});
return;
}
}

this.buttonAble = true;
let clientObj = JSON.parse(JSON.stringify(this.formData));
if(this.formData.ID == 0){
clientObj.LinkMans.push(this.linkMan);
}
clientObj.GetDate = dateFormat(clientObj.GetDate, "YYYY-MM-DD HH:mm:ss");
if(this.formData.ID == 0){
if(clientObj.LinkMans[0].Birthday){
clientObj.LinkMans[0].Birthday = clientObj.LinkMans[0].Birthday + " 00:00:00";
}
}

this.$vux.loading.show({
text: "提交中..."
});
this.postAxios("/Customer/SaveCustomer", clientObj)
.then(res => {
this.$vux.loading.hide();
this.$vux.toast.show({
text:
this.pageView === "add" ? "新建客户成功!" : "修改客户信息成功",
position: "middle",
type: "success"
});
this.backConfirm = true
this.$bus.emit("onSave")
this.setSelectCustomer({ID:res.Data, CustomerName: clientObj.CustomerName});
setTimeout(() => {
this.$vux.toast.hide();
this.$router.back();
}, 500);
})
.catch(err => {
this.$vux.loading.hide();
this.buttonAble = false;
this.$vux.toast.show({
text: err.ErrorMessage,
position: "middle",
type: "cancel"
});
});
},
changeUser(){
if(this.formData.IsReport==1){
this.$vux.toast.show({
text:'已报备的客户不能修改所有者',
position: "middle",
type: "cancel"
});
return false
}
this.userBool=true
}
},
watch: {

customerArea: function(val) {
this.formData.Province = val.province;
this.formData.City = val.city;
this.formData.District = val.area;
},
isRegister:function(val, oldVal){
if(val==0){
this.Mdt = ''
this.formData.CustomerName = ''
this.formData.CustCompID = ''
}
}









}
};



